.other-locations-container {
    width: 100%;

    .other-location {
        display: flex;
        margin: 20px;
        padding: 10px 30px;
        border-radius: 30px;
        cursor: pointer;
        background: rgb(26,19,48);
        background: linear-gradient(45deg, rgba(26,19,48,1) 0%, rgba(27,23,65,0.5046393557422969) 100%);
        border: 1px solid #6642f5;
        transition: all .2s ease-out;
    
        &:hover {
            background: rgb(255,58,105);
            background: linear-gradient(45deg, rgba(255,58,105,1) 0%, rgba(255,177,109,1) 100%);
            color: #0c0c1c;
            border: 1px solid transparent;
            -webkit-box-shadow: 0px 10px 20px 10px rgba(0,0,0,0.25); 
            box-shadow: 0px 10px 20px 10px rgba(0,0,0,0.25);
    
            .weather-icon path {
                fill: #0c0c1c;
            }
        }
    
        h1 {
            font-size: 48px;
        }

        .other-location-content {
            display: flex;
            flex-direction: column;
            margin: 0 0 0 25px;
            justify-content: center;
        
            h2, h3 {
                margin: 0;
                font-weight: 400;
            }
        
            .other-location-footer {
                width: 125%;
                display: flex;
            
                h3 {
                    margin: 0 2.5px;
                    text-transform: capitalize;
                    opacity: .75;
                }
        
                .weather-icon {
                    display: block;
                    width: 24px;
                    height: 24px;
                }
            }
        }
    }
    
    .no-results {
        text-align: center;
        width: 50%;
        margin: 50px auto;
    }
}